/*
.to-top
.to-top-container
*/

.to-top {
  width: 100%;
  height: 50px;
  position: fixed;
  bottom: 0;
  background-color: transparent;
}

.to-top-container {
  display: flex;
  justify-content: end;
  margin-bottom: 20px;
}

